<script>
    export let marker = undefined;
    export let map, lat, lng, labelText;
    export let selected = false;

    import {createEventDispatcher} from 'svelte'
    import {tweened} from "svelte/motion";
    import {cubicOut} from "svelte/easing";

    const dispatch = createEventDispatcher();
    let prevLatLng = undefined;
    export let directionDeg = 0;
    let destroy;
    let markerProto;

    let posAnim = (initial) => tweened(initial, {
        duration: 1000,
        easing: cubicOut
    });
    let latAnim = posAnim(lat);
    let lngAnim = posAnim(lng);
    $: latAnim.set(lat);
    $: lngAnim.set(lng);
    $: {
        if (marker) {
            marker.setLatLng([$latAnim, $lngAnim]);
        }
    }
    $: {
        if (selected && map)
            map.panTo(new L.LatLng(lat, lng))
    }
    let img;
    $: img = directionDeg ? "data:image/svg+xml;base64,PHN2ZyBoZWlnaHQ9IjIwIiB3aWR0aD0iMjAiIHZpZXdCb3g9Ii01IC01IDE0IDEwIiB4bWxucz0iaHR0cDovL3d3dy53My5vcmcvMjAwMC9zdmciPgogIDxwYXRoIGZpbGw9IiMzOGUiIGQ9Ik0tNSwtNSBMMTAsMCAtNSw1IDAsMCBaIi8+Cjwvc3ZnPgo=" : "data:image/svg+xml;base64,"

    $: {
        if (marker) {
            let background = selected ? "rgba(255,255,255,1);z-index:1000" : "rgba(255,255,255,0.5)"
            marker.setIcon(L.divIcon({
                html: (`<div style="border: 1px solid rgba(10, 10, 10, 0.5); color:black; font-weight: bold;`
                    + `height: 30px; border-radius: 8px;border-bottom-left-radius:0;display:flex;flex-direction: row;justify-content:space-between;`
                    + `align-items:center;background:${background};"><div style="margin-left:5px;width:20px;display:flex;flex-direction:row;align-items: center;">`
                    + `<img style="transform: rotate(${directionDeg - 90}deg);height:15px;width:15px;" src="${img}"></div>`
                    + `<div style="margin-right: 5px;">${labelText}</div></div><div style="position:absolute;bottom:0;left:0;width: 4px;height:4px;background: black;"></div>`),
                iconSize: [labelText.length * 6.6 + 30, 30],
                iconAnchor: [0, 30]
            }));
        }
    }

    function createMarker(markerElement) {
        marker = L.marker([$latAnim, $lngAnim]).addTo(map);
        let destroy = () => {
            marker.remove();
            marker = undefined;
        }
        marker.on('click', () => {
            dispatch('click')
        })
        if (marker.start !== undefined)
            marker.start();
        return {
            destroy
        }
    }
</script>
<div use:createMarker></div>
